.main{
  width: 100%;

}

.main div{
  /* background: #000; */
  margin: 0 auto;

}

.heading{
  color: white;
  text-align: center;
  font-size:3em ;
  text-transform: uppercase;
  font-weight: 500;

 
}

.todo-card{
  background: #ffffff;
  width: 550px;
  min-height: 700px;
  padding-top: 30px;
  padding-bottom: 30px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  margin-bottom: 50px;
}
.input-div{
  /* background: #000; */
  width: 90%;
  height: 60px;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-around;
  
}

.input-div input {
  width: 80%;
  height: 80%;
  border-radius: 2px;
  border: 1px solid #000;
  font-size: 2em;
  padding-left: 30px;

  
}
.input-div input:focus{
  outline:none!important;
  border: 2px solid #48dbfb;
}

.input-div button{
  width: 50px;
  height: 50px;
  background: #5f27cd;
  color: white;
  font-size: 2.5em;
  border: none;
  border-radius: 5px;


}
.input-div button:hover{
  cursor: pointer;
  color: #5f27cd;
  background: #48dbfb;
}

.todo-list{
  /* background: pink; */
  width: 90%;
 height: max-content;


}

.todo-list ul {
  /* background: pink; */
  font-size: 2.5em;
  list-style: none;
  padding: 5;
  /* background: pink; */
  width: 90%;
  
}
.list-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px !important;

}
.list-container  li {
  width: 60%;
  /* background: red; */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
 
}

.list-container div {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  /* background: green; */

}

.delete{
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 5px;
  background: red;
  color: white;
  transition: all ease-in .1s;
}
.delete:hover{
  cursor: pointer;
}
.delete:active{
  transform: scale(0.9);

}
.edit{
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 5px;
  background: #48dbfb;
  color: white;
}
.edit:hover{
  cursor: pointer;
}
.edit:active{
  transform: scale(0.9);

}

.edit-input{  width: 80%;
  height: 50px;
  border-radius: 2px;
  border: 1px solid #b199e1;
  font-size: 1em;
  padding-left: 10px;
}
.edit-input:focus{
  outline: 1px solid #48dbfb;

}

